.footer {
  background-color: #061012;

  &__wrapper {
    display: flex;
    width: 100%;
    max-width: 100%;
    justify-content: space-between;
    align-items: center;
    @include m($md) {
      flex-direction: column-reverse;
    }
  }
  .menu {
    display: flex;
    justify-content: space-between;
    @include m($xl) {
      flex-direction: column;
      padding: 15px 0;
      .footer__logo {
        margin-bottom: 23px;
      }
      height: fit-content;
    }
    @include m($md) {
      .footer__logo {
        margin-bottom: 15px;
      }
    }

    &__social {
      height: 20px;
      a {
        width: 24px;
        height: 24px;
        @include m($md) {
          width: 20px;
          height: 20px;
        }
      }
      svg {
        @include m($md) {
          width: 20px;
          height: 20px;
        }
      }
      @include m($md) {
        margin-bottom: 10px;
      }
    }
    @include m($md) {
      padding: 15px 0 10px;
    }
  }
}